export default {
	data() {
		return {
			coins: [],
			coinName: '',
			coinIndex: -1
		}
	},
	onLoad() {
		this.getCoinList()
		this.getUseCoin()
	},
	methods: {
		// 获取硬币列表
		getCoinList() {
			let list = [{
					name: '2020鼠',
					src: '2020shu'
				},
				{
					name: '2019猪',
					src: '2019zhu'
				},
				{
					name: '2018狗',
					src: '2018gou'
				},
				{
					name: '2017鸡',
					src: '2017ji'
				},
				{
					name: '2016猴',
					src: '2016hou'
				},
				{
					name: '2015羊',
					src: '2015yang'
				}
			]
			this.coins = []
			for (let item of list) {
				let data = {
					name: item.name,
					id: item.src,
					src: `http://q74m0xojb.bkt.clouddn.com/img/${item.src}_back.png`
				}
				this.coins.push(data)
			}
		},
		// 获取当前使用硬币
		getUseCoin() {
			this.coinName = uni.getStorageSync('coinName') || '2020shu'
		},
		// 选中硬币
		selectCoin(item, index) {
			this.coinIndex = index
			this.coinid = item.id
		},
		// 设置硬币皮肤
		setCoin() {
			uni.setStorageSync('coinName', this.coinid);
			uni.showToast({
				title: '设置成功',
				duration: 2000
			});
			this.getUseCoin()
		}
	}
}
